.loading {
  --speed-of-animation: .9s;
  --gap: 6px;
  --first-color: #4c86f9;
  --second-color: #49a84c;
  --third-color: #f6bb02;
  --fourth-color: #f6bb02;
  --fifth-color: #2196f3;
  width: 100px;
  height: 100px;
  justify-content: center;
  align-items: center;
  gap: 6px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.loading span {
  width: 4px;
  height: 50px;
  background: var(--first-color);
  animation: scale var(--speed-of-animation) ease-in-out infinite;
}

.loading span:nth-child(2) {
  background: var(--second-color);
  animation-delay: -.8s;
}

.loading span:nth-child(3) {
  background: var(--third-color);
  animation-delay: -.7s;
}

.loading span:nth-child(4) {
  background: var(--fourth-color);
  animation-delay: -.6s;
}

.loading span:nth-child(5) {
  background: var(--fifth-color);
  animation-delay: -.5s;
}

@keyframes scale {
  0%, 40%, 100% {
    transform: scaleY(.05);
  }

  20% {
    transform: scaleY(1);
  }
}

